<template>
  <modal :show.sync="show" v-ref:modal big backdrop="false">
    <header slot="modal-header" class="modal-header">
      <div class="auto">
        <h5 style="text-align: right" class="modal-title" > <span style="color: red;">{{timeout}}</span></h5>
        <h4 style="font-size:2;font-weight: bolder;text-align: center" class="modal-title"> {{msg}}</h4>
        <div class="row text-center">
          <button type="button"  class="btn  btn-primary" @click="print()"> 打印退出</button>

          <button type="button"  class="btn  btn-info"  @click="back()">直接退出</button>
        </div>
      </div>

   </header>
   <article slot="modal-body" class="modal-body">
     <div style="text-align: center;">
      <img class="img-responsive" :src="advertpath" @click="close()"  alt="" />
     </div>
   </article>
   <footer slot="modal-footer" class="modal-footer" >
     <!--<span>点击图片退出</span>-->
     <!-- <button v-show="show" type="button" class="btn btn-default" @click='close()'>取消</button>
     <button v-show="show" type="button" class="btn btn-success" @click='confirm()'>提交</button> -->
   </footer>
  </modal>
</template>
<script>
export default {
  title: '二维码',
  data () {
    return {
      ok: true,
      imgpath: '',
      hasClick:false,
      items: {}
    }
  },
  //  接收从微信页面传递过来的数据
  props: {
    show:{
      type: Boolean,
      default: false
    },
    advertpath : "",
    timeout: {
      type: Number,
      default: 0
    },
    msg:""
  },


  methods: {
    print (row) {
      if(!this.hasClick){
        this.hasClick = true
        this.$parent.doprint();
      }
    },
    back () {
      if(!this.hasClick){
        this.hasClick = true
        this.$parent.back('tomain');
      }

    }
  }

}
</script>



